/*----------基本設定----------*/
* {
  position: relative;
  box-sizing: border-box;
  /*&:after {
      content: '';
      clear: both;
      display: block;
  }*/
}

html {
  position: relative;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

body {
  font-family: Poppins, Arial, "GenJyuuGothicX-Medium", "cwTeXYen", "微軟正黑體", "Microsoft JhengHei", "新細明體";
  background: #f5f5f5;
  font-size: 18px;
  line-height: 1.6em;
  color: #454545;
  margin: 0 auto;
  padding: 0;
  width: 100%;
  min-height: 100%;
  position: relative;
  outline: none;
}

a {
  color: #4BBAD6;
  text-decoration: none;
  transition: ease 0.3s;
  outline: none;
}

a:hover {
  text-decoration: none;
  color: #E3964B;
}

::selection {
  background-color: #E3964B;
  color: #ffffff;
}

::-moz-selection {
  background-color: #E3964B;
  color: #ffffff;
}

:focus {
  outline: none;
}

img {
  border: none;
  padding: 0;
  margin: 0;
  display: inline-block;
  max-width: 100%;
  height: auto;
}

h1 {
  font-size: 1.4em;
  line-height: 26px;
  padding: 10px 0px 5px 0px;
}

h2 {
  font-size: 1.3em;
  line-height: 24px;
  padding: 10px 0px 5px 0px;
}

h3 {
  font-size: 1.2em;
  padding: 10px 0px 5px 0px;
}

h4 {
  font-size: 1em;
  padding: 10px 0px 5px 0px;
}

p {
  padding: 5px 0px;
}

time {
  display: block;
}

table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  border-radius: 5px;
  overflow: hidden;
}
table.nowrap {
  white-space: nowrap;
}
table thead {
  background-color: #E3964B;
}
table thead tr,
table thead th,
table thead td {
  color: #ffffff;
  font-weight: bold;
}
table tbody tr {
  transition: ease 0.3s;
}
table tbody tr:nth-child(odd) {
  background-color: #f5f5f5;
}
table tbody tr:hover {
  background-color: #eeeeee;
}
table tr,
table th,
table td {
  padding: 6px 10px;
  text-align: left;
  border-bottom: #eeeeee solid 1px;
}
table tr .alignCenter,
table th .alignCenter,
table td .alignCenter {
  text-align: center;
}
table tr .alignRight,
table th .alignRight,
table td .alignRight {
  text-align: right;
}

ul {
  margin-left: 20px;
  padding-top: 10px;
}
ul li {
  list-style: disc;
  padding: 0px 0px 8px 0px;
  margin: 0;
}

ol {
  margin-left: 20px;
  padding-top: 10px;
  display: block;
}
ol li {
  display: block;
  list-style: decimal;
  padding: 0px 0px 8px 0px;
  margin: 0;
}

i {
  padding: 0 5px;
  vertical-align: middle;
}

sub,
sup {
  font-size: 0.7em;
}

/*----------表單設定----------*/
input,
textarea,
select {
  font-family: Poppins, Arial, "GenJyuuGothicX-Medium", "cwTeXYen", "微軟正黑體", "Microsoft JhengHei", "新細明體";
  border: none;
  border-bottom: #cccccc solid 1px;
  background: #ffffff;
  padding: 10px 0;
  font-size: 1em;
  color: #454545;
  box-sizing: border-box;
  border-radius: 0px;
  transition: linear 0.3s;
  outline: none;
  width: 100%;
  margin: 0;
}
input:hover, input:focus,
textarea:hover,
textarea:focus,
select:hover,
select:focus {
  border-bottom: #E3964B solid 1px;
}

input[type=button],
input[type=submit],
input[type=reset] {
  border: none;
  background: #E3964B;
  color: #ffffff;
  width: 120px;
  margin: 5px 2px;
  padding: 10px;
  border-radius: 50px;
  cursor: pointer;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}
input[type=button]:hover,
input[type=submit]:hover,
input[type=reset]:hover {
  background-color: #4BBAD6;
}
input[type=button]:disabled,
input[type=submit]:disabled,
input[type=reset]:disabled {
  background-color: #eeeeee;
}

textarea {
  margin: 0 0 3px 0;
  line-height: 20px;
  display: block;
}

select {
  width: 100%;
}

input[type=checkbox],
input[type=radio] {
  position: relative;
  margin-right: 10px;
  transform: scale(1.3, 1.3);
}

/*-----------loading----------*/
#loading {
  background-color: #ffffff;
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: 999999;
  margin: 0px;
  padding: 0;
  top: 0px;
}
#loading #loading-center {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 100px;
  width: 100px;
  margin-top: -50px;
  margin-left: -50px;
  text-align: center;
}
#loading #loading-center img {
  display: block;
  width: 100px;
  height: auto;
  margin: auto;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  padding: 20px 3%;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-content: center;
  z-index: 10000;
}
header figure {
  width: 60px;
}
header figure img {
  display: block;
  width: 100%;
  height: auto;
}
header h1 {
  font-size: 1.25em;
  padding-left: 10px;
  display: block;
}

nav {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 13px 3%;
  height: auto;
  background-color: #ffffff;
  box-shadow: -1px -1px 5px rgba(145, 145, 145, 0.5);
  z-index: 9999;
}
nav .menu {
  max-width: 300px;
  margin: auto;
  display: flex;
  justify-content: center;
  flex-wrap: nowrap;
}
nav .menu .menuList {
  text-align: center;
  padding: 0 10px;
}
nav .menu .menuList a {
  display: block;
  color: #454545;
}
nav .menu .menuList a i {
  display: block;
  font-size: 1.2em;
}
nav .menu .menuList a:hover {
  color: #4BBAD6;
}

footer {
  text-align: center;
  font-size: 0.7em;
  color: #999999;
  padding-top: 30px;
  line-height: 1.3em;
}

.error-message {
  font-size: 14px;
  color: #E3964B;
  padding-left: 5px;
}

.indexRecord {
  position: absolute;
  width: 500px;
  height: 340px;
  left: 50%;
  top: 50%;
  margin-left: -250px;
  margin-top: -170px;
}
@media only screen and (max-width: 600px) {
  .indexRecord {
    width: 94%;
    margin-left: -47%;
  }
}
.indexRecord .todayBtn {
  width: 100px;
  margin: auto;
  padding-bottom: 20px;
}
.indexRecord .todayBtn a {
  display: block;
  background-color: #E3964B;
  color: #ffffff;
  text-align: center;
  border-radius: 50px;
}
.indexRecord .todayBtn a:hover {
  background-color: #4BBAD6;
}
.indexRecord .timePicker {
  width: 250px;
  margin: auto;
}
.indexRecord .timePicker time {
  text-align: center;
  font-size: 1em;
}
.indexRecord .recordArea {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
  padding-top: 50px;
}
.indexRecord .recordArea .recordList {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background-color: #ffffff;
  text-align: center;
  box-shadow: rgba(51, 51, 51, 0.1) 1px 1px;
  cursor: pointer;
  transition: ease 0.1s;
}
.indexRecord .recordArea .recordList:hover {
  transform: scale(0.95);
}
.indexRecord .recordArea .recordList figure {
  width: 70px;
  margin: auto;
  padding-top: 25px;
}
.indexRecord .recordArea .recordList figure img {
  display: block;
  width: 100%;
  height: auto;
}
.indexRecord .recordArea .recordList h2,
.indexRecord .recordArea .recordList h3 {
  font-size: 1em;
}
@media only screen and (max-width: 500px) {
  .indexRecord .recordArea .recordList {
    width: 130px;
    height: 130px;
  }
  .indexRecord .recordArea .recordList figure {
    width: 60px;
    padding-top: 20px;
  }
  .indexRecord .recordArea .recordList h2,
.indexRecord .recordArea .recordList h3 {
    font-size: 1em;
  }
}
@media only screen and (max-width: 430px) {
  .indexRecord .recordArea .recordList {
    width: 120px;
    height: 120px;
  }
  .indexRecord .recordArea .recordList figure {
    width: 50px;
    padding-top: 17px;
  }
}
@media only screen and (max-width: 400px) {
  .indexRecord .recordArea .recordList {
    width: 90px;
    height: 90px;
  }
  .indexRecord .recordArea .recordList figure {
    width: 40px;
    padding-top: 10px;
  }
  .indexRecord .recordArea .recordList h2,
.indexRecord .recordArea .recordList h3 {
    font-size: 0.8em;
    padding-top: 5px;
  }
}

